export default {
	data() {
		return {
			userName: '',
			pwd: '',
		}
	},
	methods: {
		login() { //点击登录
			if (this.userName === '' || this.userName.length <= 0 || this.pwd === '' || this.pwd.length <= 0) {
				uni.showToast({
					title: '请填入完整的登录信息',
					icon: "none"
				});
			}
			uni.switchTab({
				url: "../index/index"
			})
		}
	}
}
